/* 현재위치 정보를 리턴해주는 함수 */
export const fnGetLatLng = () => {
  return new Promise((resolve, reject) => {
    navigator.geolocation.getCurrentPosition(
      (location) => {//success
        resolve({
          lat: location.coords.latitude,
          lng: location.coords.longitude,
        })
      },
      (error) => {//failed
        if (error.code === 1) alert('위치 접근 권한을 허용해주세요')
        else alert('위치 정보 수신중 오류가 발생했습니다')
        reject(new Error('에러가 났습니다'))//throw new Error('에러가 났습니다')
      }
    )//getCurrentPosition method
  })
}//fnGetLatLng

/* 위경도를 받아서 구글맵을 출력하는 함수 */
export const fnInitMap = (latLngObj, fn) => {
  const { lat, lng } = latLngObj
  let map = new window.google.maps.Map(document.getElementById('map'), {
    center: { lat: lat, lng: lng },
    zoom: 8,
    styles: [
      {
        "featureType": "landscape.natural",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "visibility": "on"
          },
          {
            "color": "#e0efef"
          }
        ]
      },
      {
        "featureType": "poi",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "visibility": "on"
          },
          {
            "hue": "#1900ff"
          },
          {
            "color": "#c0e8e8"
          }
        ]
      },
      {
        "featureType": "road",
        "elementType": "geometry",
        "stylers": [
          {
            "lightness": 100
          },
          {
            "visibility": "simplified"
          }
        ]
      },
      {
        "featureType": "road",
        "elementType": "labels",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "transit.line",
        "elementType": "geometry",
        "stylers": [
          {
            "visibility": "on"
          },
          {
            "lightness": 700
          }
        ]
      },
      {
        "featureType": "water",
        "elementType": "all",
        "stylers": [
          {
            "color": "#7dcdcd"
          }
        ]
      }
    ]
  })//map

  let center = { lat: lat, lng: lng }
  let marker = new window.google.maps.Marker({ position: center, map: map });

  map.addListener('click', async (e) => {
    const result = window.confirm('해당위치로 날씨정보를 갱신하시겠습니까?')
    if (result) {
      let lat = e.latLng.lat()
      let lng = e.latLng.lng()
      marker.setPosition({ lat, lng })
      fn({ lat, lng })
    }
  })//click
}//fnInitMap

/* 위경도를 받아서 주소를 반환하는 함수 */
export const fnGetAddress = (latLngObj,lang) => {
  return new Promise((resolve) => {
    let geocoder = new window.google.maps.Geocoder;
    let address
    geocoder.geocode({'location': latLngObj , 'language':lang}, function (results, status) {
      try {
        address = `[ ${results[3].formatted_address} ]`
      } catch {//formatted_address정보가 없을 경우 예외처리
        address = (lang==='ko')
          ? `해당위치의 주소는 존재하지 않습니다`
          : `Failed to get address information for that location`
      }
      resolve(address)
    })//geocode

  })//Promise
}//fnGetAddress








